html {
  scroll-behavior: smooth;
}
body {
  font-family: var(--font-sans);
  background-color: var(--color-surface-bg);
  color: var(--color-text-primary);
  overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--color-surface-panel);
}
::-webkit-scrollbar-thumb {
  background: var(--color-surface-stroke-light);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--color-primary);
}
::selection {
  background-color: var(--color-primary);
  color: white;
}
:focus-visible {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
}
.gradient-text {
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-accent-gold) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.glow-primary {
  box-shadow: 0 0 30px rgba(220, 123, 57, 0.4);
}
.glow-gold {
  box-shadow: 0 0 30px rgba(218, 165, 32, 0.4);
}
.glass {
  background: rgba(17, 20, 22, 0.8);
  backdrop-filter: blur(12px);
  border: 1px solid var(--color-surface-stroke);
}
.card-hover {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.card-hover:hover {
  transform: translateY(-8px);
  box-shadow: 0 22px 48px rgba(0, 0, 0, 0.42);
}
.gradient-border {
  position: relative;
  background: var(--color-surface-panel);
  border-radius: 12px;
}
.gradient-border::before {
  content: '';
  position: absolute;
  inset: -1px;
  border-radius: 13px;
  background: linear-gradient(135deg, var(--color-primary), var(--color-accent-gold), var(--color-primary));
  background-size: 200% 200%;
  animation: gradient-rotate 3s linear infinite;
  z-index: -1;
}
@keyframes gradient-rotate {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
html.lenis, html.lenis body {
  height: auto;
}
.lenis.lenis-smooth {
  scroll-behavior: auto !important;
}
.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}
.lenis.lenis-stopped {
  overflow: hidden;
}
.lenis.lenis-smooth iframe {
  pointer-events: none;
}
